.stars {
  display: flex;

  &__star {
    display: flex;
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;

  }

  &__star:not(:last-child) {
    margin-right: 4px;
  }

  &--4 .stars__star:nth-child(-n+4) {
    display: flex;
    flex-direction: row;
    background-image: url(../images/star-active.svg);
  }
}
